import React from 'react';

export const Separator = (props) => {
    const separatorStyle = {
        display: 'block',
        margin: '2rem 0',
        borderBottom: '1px solid #E3E3E3'
    }
    return (
        <div style={separatorStyle}></div>
    );
}


